.App_header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  position: absolute;
  width: 100vw;
}
.fram1C {
  background: linear-gradient(0deg, rgb(255 255 255 / 0%), rgba(0, 0, 0, 0.2)),
    url("../../Images/feam2_9.png");
    background-repeat:no-repeat;
  background-repeat: round;
  background-repeat:no-repeat;
  display: grid;
  grid-template-columns: auto;
  justify-items: center;
}
.textH01 {
  width: 70vw;
}
.textCustomerH {
  display: contents;
  color: #ffffff;
  font-family: "DBHeaventLi";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
}
.fram1_button2 {
  background: transparent;
  border-radius: 30px;
  font-weight: 300;
  font-size: 26px;
  line-height: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  border: 1px solid #ffffff;
  margin: 20px;
  width: 13vw;
}
.H_text01 {
  background: linear-gradient(
    91.8deg,
    #0097fe 1.03%,
    #0075ff 1.04%,
    #9e70ff 99.01%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-family: "DBHeavent";
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
}
